  async changePassword(cpi: ChangePasswordInfo): Promise<OperationResult> {
    const bodyString = JSON.stringify(cpi);
    const headers = new HttpHeaders({ 'Content-Type': 'application/json' });
    // tslint:disable-next-line: align
    return this.http
      .post<OperationResult>(this.getChangePasswordApiUrl(), bodyString, { headers })
      .pipe(
        map((res: any) => {
          if (!res || !res.Result) {
            this.errorMessage = res.Message + ' (Code:' + res.Code + ')';
            return res;
          }
          return res;
        }),
        catchError((error: HttpErrorResponse) => {
          console.error(`Error Code: ${error.status}\nMessage: ${error.message}`);
          const res = new OperationResult();
          res.Code = 662;
          return of(res);
        })
      )
      .toPromise();
  }

   async resendOTP(accname: string): Promise<OperationResult> {
    const headers = new HttpHeaders({ 'Content-Type': 'application/json' });
    return this.http
      .post<OperationResult>(this.resendOTPUrl() + accname, { headers })
      .pipe(
        map((res: any) => {
          if (!res) {
            res = new OperationResult();
            res.Code = 663;
          }
          return res;
        }),
        catchError((error: HttpErrorResponse) => {
          console.error(`Error Code: ${error.status}.\nMessage: ${error.message}`);
          const res = new OperationResult();
          res.Message = `Error Code: ${error.status}.\nMessage: ${error.message}`;
          res.Code = 669;
          return of(res);
        })
      )
      .toPromise();
  }

  async resetpassword(accname: string): Promise<OperationResult> {
    const headers = new HttpHeaders({ 'Content-Type': 'application/json' });
    // tslint:disable-next-line: align
    return this.http
      .post<OperationResult>(this.getResetPasswordUrl() + accname, { headers })
      .pipe(
        map((res: any) => {
          if (!res) {
            res = new OperationResult();
            res.Code = 663;
          }
          return res;
        }),
        catchError((error: HttpErrorResponse) => {
          console.error(`Error Code: ${error.status}.\nMessage: ${error.message}`);
          const res = new OperationResult();
          res.Message = `Error Code: ${error.status}.\nMessage: ${error.message}`;
          res.Code = 661;

          if (!this.router.routerState.snapshot.url.includes(this.getLoginPageUrl()))
            this.router.navigate([this.getLoginPageUrl()]);
          return of(res);
        })
      )
      .toPromise();
  }

  public logoff() {
    const logoffRequest: LogoffRequest = new LogoffRequest(this.getToken());

    return this.http
      .post<LogoffResponse>(this.getLogoutUrl(), logoffRequest)
      .pipe(
        map((logoffResponse: LogoffResponse) => {
          if (logoffResponse.Result) {
            console.log('AuthService: Clearing storage due to Logoff');
            this.clearStorage();
            this.loggedOut$.next();
          }

          return logoffResponse;
        })
      )
      .toPromise();
  }

  public navigateUserGateway() {
    console.log('entering navigateUserGateway..');
    let userGatewayUrl = this.getUserGatewayUrl();

    // if usergateway url exists, then redirect to it
    if (userGatewayUrl !== '') {
      console.log(`Found getUserGatewayUrl ${userGatewayUrl}`);
      document.location.href = userGatewayUrl;
      return;
    }

    // otherwise, redirect to login
    this.router.navigate([this.getLoginPageUrl()]);
  }

  public getRedirectUrlForSubscription(accountName: string, subscriptionKey: string) {
    this.getInstancesMapForUser(accountName).subscribe(
      (res) => {
        const map: Array<{ SubscriptionKey: string; Description: string; InstanceKey: string }> = res as Array<{
          SubscriptionKey: string;
          Description: string;
          InstanceKey: string;
        }>;
        if (!map || map.length === 0) {
          throw 'instanceMap is invalid';
        }
        const currentInstanceKey: string = map.filter((k) => k.SubscriptionKey === subscriptionKey).map((j) => j.InstanceKey)[0];
        this.getSnapshot(currentInstanceKey, subscriptionKey).subscribe(
          (res) => {
            if (!res || res === [] || res.length === 0) throw 'snapshot is empty';
            // we have now the snapshot
            const services: Array<{ ServiceType: string; Url: string; ProductVersion: string; Port: number }> = res[
              'Services'
            ] as Array<{ ServiceType: string; Url: string; ProductVersion: string; Port: number }>;

            let redirectUrl: string = services
              .filter((i) => i.ServiceType === 'M4FRONTEND' || i.ServiceType === 'APP_FRONTEND')
              .map((f) => f.Url)[0];
            console.log(`Designated redirect is ${redirectUrl}`);
            const baseRedirectUrl = `${redirectUrl}?jwt=${this.getToken()}&subKey=${subscriptionKey}&instanceKey=${currentInstanceKey}`;

            console.log(`Designated final redirect is ${baseRedirectUrl}`);
            localStorage.setItem('lastLoggedRedirect', baseRedirectUrl);
            document.location.href = 'http://' + baseRedirectUrl;
          },
          (err) => {
            console.log('snapshot cannot be obtained');
            throw 'snapshot cannot be obtained';
            //this.router.navigate([this.getRedirectUrl()]);
          }
        );
      },
      (err) => {
        console.log('getRedirectUrlForSubscription ia about to fail...');
        console.log(err);
        throw 'getInstancesMapForUser failed';
      }
    );
  }
  public getInstancesMapForUser(user: string) {
    return this.http.get<OperationResult>(this.getInstancesMapForAccountUrl() + user).pipe(
      map((res: any) => {
        if (!res || !res.Result) return [];
        return res.Content;
      })
    );
  }

