import { Component, EventEmitter, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialogModule, MAT_DIALOG_DEFAULT_OPTIONS } from '@angular/material/dialog';
@Component({
  selector: 'otp-dialog',
  templateUrl: './otp.component.html',
  styleUrls: ['./otp.component.css']
})

export class OtpComponent implements OnInit {

  public title: string;
  public accname: string;
  public message: string;
  public placeHolder: string;
  public inputValue: string;
  public buttonCancel: string;
  public resendOTPpLabel: string;
  resendRequested = new EventEmitter();
  constructor(
    public mdDialogRef: MatDialogRef<OtpComponent>,
    @Inject(MAT_DIALOG_DATA) private data: any
  ) {
    this.title = data.Title;
    this.accname = data.AccountName;
    this.message = data.Message;
    this.placeHolder = data.PlaceHolder;
    this.inputValue = data.TextValue;
    if (navigator.language === 'it' || navigator.language === 'it-IT' || navigator.language === 'it-CH') {
      this.buttonCancel = 'Annulla';
      this.resendOTPpLabel  = 'Inviami un nuovo codice';
    } else {
      this.buttonCancel = 'Cancel';
      this.resendOTPpLabel  = 'Send me a new code';
    }
  }

  ngOnInit() { }

  public cancel() {

    this.data = undefined;
    this.mdDialogRef.close(this.data);
  }

   public async resendOTP() {
     this.resendRequested.emit();
  }
    public closeDialog() {
  this.data.TextValue = this.inputValue;
  this.mdDialogRef.close(this.data);
  }
}
